import { LeftOutlined } from "@ant-design/icons";
import React from "react";
import { Link } from "react-router-dom";
import Header from "./Header";
import DebitCard from "./SVG Images/path54376.svg";

const Payment = () => {
  return (
    <div>
      <Header />
      <div className="container">
        <div className="payment">
          <div className="page-heading">
            <p>
              <Link to="/" className="home-link">
                <LeftOutlined className="mx-3" /> Go to Home page
              </Link>
            </p>
          </div>
          <div className="row p-md-5 p-sm-2 card-check">
            <div className="col-md-6 px-5 col-sm-12">
              <form>
                <label>Card holder name</label>
                <input
                  type={"text"}
                  className="form-control"
                  placeholder="Type your full name"
                />
                <label>Card number</label>
                <input
                  type={"text"}
                  className="form-control"
                  placeholder="Type your full name"
                />
                <div className="row">
                  <div
                    className="col-md-6 col-sm-6"
                  >
                    <label>Expiry date</label>
                    <input type={"month"} className="form-control" />
                    <div className="py-2">
                      <p
                        style={{
                          color: "#747474",
                          fontFamily: "Poppins , sans-serif",
                          fontWeight: "300",
                          textAlign: "end",
                        }}
                      >
                        Total payment (100%):
                      </p>
                      <p
                        style={{
                          color: "#222222",
                          fontFamily: "Poppins , sans-serif",
                          fontWeight: "300",
                          textAlign: "end",
                          fontSize: "13px",
                        }}
                      >
                        The amount you have to pay (30%):
                      </p>
                    </div>
                  </div>
                  <div
                    className="col-md-6 col-sm-6"
                   
                  >
                    <label>CVV code</label>
                    <input type={"password"} className="form-control" />
                    <div className="py-2">
                      <p>
                        <small
                          style={{
                            fontFamily: "Poppins , sans-serif",
                            fontWeight: "300",
                            textAlign: "start",
                            fontSize: "15px",
                          }}
                        >
                          $50 000
                        </small>
                      </p>
                      <p>
                        <b>$15 000</b>
                      </p>
                    </div>
                  </div>
                </div>
                <button id="button1">Submit</button>
              </form>
            </div>
            <div className="col-md-6 px-5 col-sm-12">
              <img src={DebitCard} alt="card" className="py-5" />
              <p className="py-5">
                <small>
                  You must spend 30 % of the total amount to get started.
                </small>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Payment;
