import { LeftOutlined } from "@ant-design/icons";
import React, { useState } from "react";
import "./Login.css";
import "./LoginResponsive.css";
import Darklogo from "../SVG Images/logo-dark.svg";
import { Link } from "react-router-dom";
import SignUp from "./SignUp";
import SignIn from "./SignIn";

const LogIn = () => {
  const [signPage, setSignPage] = useState(true);
  // const users = [
  //   {
  //     name: "Aziz",
  //     lastname: "Rahimov",
  //     email: "azizr@gmail.com",
  //     password: "1122",
  //     tel: "935006777",
  //   },
  //   {
  //     name: "Doniyor",
  //     lastname: "Asqarov",
  //     email: "doniyor@gmail.com",
  //     password: "1234",
  //     tel: "946406101",
  //   },
  //   {
  //     name: "Admin",
  //     lastname: "Admin",
  //     email: "admin@admin.com",
  //     password: "12345678",
  //     tel: "977776688",
  //   },
  // ];
  // localStorage.setItem("users", JSON.stringify(users));

  const Signup = (e) => {
    setSignPage(true);
    e.preventDefault();
  };
  const Signin = (e) => {
    setSignPage(false);
    e.preventDefault();
  };


  return (
    <section id="login">
      <div className="container">
        <div className="back">
          <button>
            <Link to="/" className="btn-link">
              <LeftOutlined className="px-3" />
              Back
            </Link>
          </button>
        </div>
        <div className="logo">
          <img src={Darklogo} alt="brandname" />
        </div>
        <div className="col-md-4 offset-md-4 col-sm-12">
          <form className="form-control">
            <div className="form-buttons">
              <button
                onClick={Signup}
                className={signPage ? "btn-active" : "btn-unactive"}
              >
                Kirish
              </button>
              <button
                onClick={Signin}
                className={signPage ? "btn-unactive" : "btn-active "}
              >
                Ro'yxatdan o'tish
              </button>
            </div>
            {signPage ? 
               <SignUp/>
             : 
             <SignIn/>
            }
          </form>
        </div>
      </div>
    </section>
  );
};

export default LogIn;
