import {
  DownOutlined,
  LeftOutlined,
  LockOutlined,
  LogoutOutlined,
  MailOutlined,
  MobileOutlined,
  ShoppingOutlined,
  UserOutlined,
} from "@ant-design/icons";
import React from "react";
import { Link } from "react-router-dom";
import Header from "./Header";
import UserImg from "./SVG Images/Users-User-icon.png";

const Settings = () => {
  return (
    <div>
      <Header />
      <div className="container">
        <div className="settings">
          <div className="page-heading">
            <p>
              <Link to="/profile" className="home-link">
                <LeftOutlined className="mx-3" />
                Back
              </Link>
            </p>
            <h1 className="page-title py-5">Settings</h1>
          </div>
          <div className="user row">
            <div className="col-md-6 col-sm-12 user-features">
              <img src={UserImg} alt="user" />
              <div className="username">
                <h4>Asqarov Doniyor</h4>
                <p>blahblahblahatdotcom@gmail.com</p>
              </div>
            </div>
          </div>

          <div className="col-12 my-5">
            <div className="user-info">
              <div className="left-side">
                <UserOutlined style={{ fontSize: "25px", color: "#004267" }} />
                <h5>Your name</h5>
                <p>Asqarov Doniyor</p>
              </div>
              <div className="right-side">
                <button>
                  <DownOutlined />
                </button>
              </div>
            </div>
          </div>

          <div className="col-12 my-5">
            <div className="user-info">
              <div className="left-side">
                <MailOutlined style={{ fontSize: "25px", color: "#004267" }} />
                <h5>E-mail</h5>
                <p>blahblahblahatdotcom@gmail.com</p>
              </div>
              <div className="right-side">
                <button>
                  <DownOutlined />
                </button>
              </div>
            </div>
          </div>

          <div className="col-12 my-5">
            <div className="user-info">
              <div className="left-side">
                <MobileOutlined
                  style={{ fontSize: "25px", color: "#004267" }}
                />
                <h5>Phone number</h5>
                <p>(998)94 640-61-01</p>
              </div>
              <div className="right-side">
                <button>
                  <DownOutlined />
                </button>
              </div>
            </div>
          </div>

          <div className="col-12 my-5">
            <div className="user-info">
              <div className="left-side">
                <LockOutlined style={{ fontSize: "25px", color: "#004267" }} />
                <h5>Change password</h5>
                <p>...............</p>
              </div>
              <div className="right-side">
                <button>
                  <DownOutlined />
                </button>
              </div>
            </div>
          </div>

          <div className="col-12 my-5">
            <div className="user-info">
              <div className="left-side">
                <ShoppingOutlined
                  style={{ fontSize: "25px", color: "#004267" }}
                />
                <h5>My orders</h5>
                <p>7 orders</p>
              </div>
              <div className="right-side">
                <button>
                  <DownOutlined />
                </button>
              </div>
            </div>
          </div>

          <button style={{ border: "0", background: "none", margin: "20px 0" }}>
            <Link
              to="/"
              style={{
                color: "#FF001E",
                textDecoration: "none",
                fontSize: "20px",
              }}
            >
              <LogoutOutlined /> Log out
            </Link>
          </button>
        </div>
      </div>
    </div>
  );
};

export default Settings;
