import React, { useState } from "react";
import { NavLink, Link } from "react-router-dom";
import Logo from "./SVG Images/logo.svg";
import { BarsOutlined, CloseOutlined } from "@ant-design/icons";


const Header = () => {
  var check = true
  const [show, setShow] = useState(true);
  window.onscroll = function (e) {
    if (this.scrollY < 100) {
      setShow(true);
    } else {
      setShow(false);
    }
  };

   const [collapse , setCollapse]=useState(true)
   const collapseNav=()=> {
    setCollapse(!collapse)
   }

  return (
    <div>
      <header className={show ? "d-block" : "d-none"}>
        <div className="container">
          <div className="row py-3">
          <div className="logo col-md-2">
                <img src={Logo} alt="brandname" />
               {
                collapse ?  <button onClick={collapseNav}><BarsOutlined/></button> :
                <button onClick={collapseNav}><CloseOutlined/></button>
               }
              </div>
            <div className="col-md-5 col-sm-12 nav" id={collapse ? "header-toggle" : "" }>
              <ul className="menu">
                <li className="menu_item">
                  <NavLink to="/" className="menu_link">
                    Home
                  </NavLink>
                </li>
                <li className="menu_item">
                  <NavLink to="/portfolio" className="menu_link">
                    Portfolio
                  </NavLink>
                </li>
                {
                  check ? 
                  <li className="menu_item">
                  <NavLink to="/orderproject" className="menu_link">
                    Order a project
                  </NavLink>
                </li> :
                <li className="menu_item">
                <NavLink to="/login" className="menu_link">
                  Order a project
                </NavLink>
              </li>
                }
               { check ?
                 <li className="menu_item">
                 <NavLink to="/paidcourses" className="menu_link">
                   Paid Courses
                 </NavLink></li> :
                 <li className="menu_item">
                  <NavLink to="/login" className="menu_link">
                    Paid Courses
                  </NavLink>
                </li>
               
               }
                {
                 check ? <li className="menu_item">
                  <NavLink to="/profile" className="menu_link">
                    Profile
                  </NavLink>
                </li> : ""
                }
              </ul>
            </div>
            <div className="col-md-5 col-sm-12 options" id={collapse ? "header-toggle" : ""}>
                <select>
                  <option>En</option>
                  <option>Ru</option>
                  <option>Uz</option>
                </select>
             { check ?
                  <Link to="/orderproject" className="menu_link link">
                  <button id="button1">Order a project</button>
                </Link>
                :
                  <Link to="/login" className="menu_link link">
                  <button id="button1">Order a project</button>
                </Link>
                }

                { check ?
                  <p style={{color: "white", fontFamily:"Poppins,sans-serif"}}>Asqarov Doniyor</p>:
                  <Link to="/login" className="link">
                  <button type="button" id="button2">
                    Register
                  </button>
                </Link>
                }
            
            
            </div>
          </div>
        </div>
      </header>
    </div>
  );
};

export default Header;
