import React  from "react";
import { Link } from "react-router-dom";
import Instagram from "./SVG Images/instagram-logo 1.svg";
import Telegram from "./SVG Images/telegram 1.svg";
import Facebook from "./SVG Images/facebook 1.svg";
import Logo from "./SVG Images/logo.svg";


const Footer = (data) => {
 
  var ischecked = true

  if(data.data){
    ischecked = data
  }

  return (
    <div>
      <footer className="footer">
        <div className="container">
          <div className="col-md-7 col-sm-12 footer-title">
            <h1>Biz bilan hamkor bo'ling</h1>
            <p>Biz sifatli va ajoyib modellarni sizga taqdim qilamiz</p>

            {
              ischecked ? 
              <Link to="/orderproject" className="link">
              <button type="button" id="button1">
                Order a project
              </button>
            </Link> :
            <Link to="/login" className="link">
            <button type="button" id="button1">
              Order a project
            </button>
          </Link>
            }
          </div>
          <div className="row">
            <div className="col-md-6 col-sm-12 nav">
              <div className="logo">
                <img src={Logo} alt="brandname" />
              </div>
              <ul className="menu">
                <li className="menu_item">
                  <Link to="/" className="menu_link">
                    Home
                  </Link>
                </li>
                <li className="menu_item">
                  <Link to="/aboutus" className="menu_link">
                    About us
                  </Link>
                </li>
                {
                  ischecked ?
                  <li className="menu_item">
                  <Link to="/orderproject" className="menu_link ">
                    Order a project
                  </Link>
                </li> :
                <li className="menu_item">
                <Link to="/login" className="menu_link ">
                  Order a project
                </Link>
              </li>
                }
                {
                  ischecked ?
                  <li className="menu_item">
                  <Link to="/paidcourses" className="menu_link">
                    Paid courses
                  </Link>
                </li> :
                <li className="menu_item">
                <Link to="/login" className="menu_link">
                  Paid courses
                </Link>
              </li>
                }
              </ul>
            </div>
            <div className="col-md-4 col-sm-12">
              <div className="links">
                <a href="https://www.instagram.com/">
                  <img src={Instagram} alt="logo" />
                </a>
                <a href="https://www.telegram.org/">
                  <img src={Telegram} alt="logo" />
                </a>
                <a href="https://www.facebook.com/">
                  <img src={Facebook} alt="logo" />
                </a>
              </div>
            </div>
          </div>
        </div>
      </footer>
    </div>
  );
};

export default Footer;
