import React, { useState } from "react";
import { DownOutlined, LeftOutlined, UpOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";
import Header from "./Header";
import Footer from "./Footer";
import Dollar from "./SVG Images/$.svg";
import ChinaTower from "./SVG Images/Rectangle 439.svg";
import SkyCrapers from "./SVG Images/Rectangle 440.svg";
import Hall from "./SVG Images/Rectangle 441.svg";

const Order = () => {
  const [open, setOpen] = useState(true);
  const [openDiv, setOpenDiv] = useState(true);
  const [num, setNum] = useState(0);
  const [num2, setNum2] = useState(0);
  const openBox = () => {
    setOpen(false);
  };
  const closeBox = () => {
    setOpen(true);
  };
  const openBox2 = () => {
    setOpenDiv(false);
  };
  const closeBox2 = () => {
    setOpenDiv(true);
  };
  const addNum = () => {
    setNum(num + 1);
  };
  const addNum2 = () => {
    setNum2(num2 + 1);
  };

  return (
    <div>
      <Header />
      <main>
        <div className="container">
          <div className="page-heading">
            <p>
              <Link to="/" className="home-link">
                <LeftOutlined className="mx-3" /> Go to Home page
              </Link>
            </p>
            <h1 className="page-title">Order a project</h1>
          </div>

          <div className="title">
            <div className="labels">
              <label>Title</label>
              <p>
                <small className="mx-4">0/80</small>
              </p>
            </div>
            <input
              type={"text"}
              className="text-space col-12"
              placeholder="Type here"
              required
            />
          </div>

          <div className="description">
            <label>Description</label>
            <textarea
              rows={"8"}
              className="text-space col-12"
              placeholder="Type here"
              required
            />
          </div>

          <div className="row">
            <div className="col-md-6 col-sm-12 myDiv">
              <label>3D skills</label>

              {open ? (
                <div className="selected_items">
                  <p>{num} selected</p>
                  <button onClick={openBox}>
                    <DownOutlined />
                  </button>
                </div>
              ) : (
                <div className="select_box">
                  <div className="select_items">
                    <div className="select_box_header">
                      <h4>Select 3D skills</h4>
                      <p>
                        <small>{num} selected</small>
                      </p>
                    </div>
                    <button>
                      <UpOutlined onClick={closeBox} />
                    </button>
                  </div>

                  <div className="row items_list">
                    <div className="col-md-5 col-sm-12 items">
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum}
                        />
                        <label htmlFor="checkbox-input">3D Modelling</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum}
                        />
                        <label htmlFor="checkbox-input">
                          3D Print Modelling
                        </label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum}
                        />
                        <label htmlFor="checkbox-input">3D Scanning</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum}
                        />
                        <label htmlFor="checkbox-input">Animating</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum}
                        />
                        <label htmlFor="checkbox-input">Lighting</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum}
                        />
                        <label htmlFor="checkbox-input">
                          Low Poly Modelling
                        </label>
                      </div>
                    </div>
                    <div className="col-md-4 col-sm-12 items">
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum}
                        />
                        <label htmlFor="checkbox-input">Particles</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum}
                        />
                        <label htmlFor="checkbox-input">PBR Modelling</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum}
                        />
                        <label htmlFor="checkbox-input">Rendering</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum}
                        />
                        <label htmlFor="checkbox-input">Rigging</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum}
                        />
                        <label htmlFor="checkbox-input">Shaders</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum}
                        />
                        <label htmlFor="checkbox-input">Skinning</label>
                      </div>
                    </div>
                    <div className="col-md-3 col-sm-12 items">
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum}
                        />
                        <label htmlFor="checkbox-input">Texturing</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum}
                        />
                        <label htmlFor="checkbox-input">UV Mapping</label>
                      </div>
                    </div>
                  </div>
                </div>
              )}
            </div>
            <div className="col-md-6 col-sm-12 myDiv">
              <label>3D software</label>

              {openDiv ? (
                <div className="selected_items">
                  <p>{num2} selected</p>
                  <button onClick={openBox2}>
                    <DownOutlined />
                  </button>
                </div>
              ) : (
                <div className="select_box">
                  <div className="select_items">
                    <div className="select_box_header">
                      <h4>Select 3D software</h4>
                      <p>
                        <small>{num2} selected</small>
                      </p>
                    </div>
                    <button onClick={closeBox2}>
                      <UpOutlined />
                    </button>
                  </div>

                  <div className="row">
                    <div className="col-md-6  col-sm-12 items">
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum2}
                        />
                        <label htmlFor="checkbox-input">Autodesk 3Ds Max</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum2}
                        />
                        <label htmlFor="checkbox-input">OBJ</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum2}
                        />
                        <label htmlFor="checkbox-input">3D Studio</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum2}
                        />
                        <label htmlFor="checkbox-input">Autodesk FBX</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum2}
                        />
                        <label htmlFor="checkbox-input">Zbrush</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum2}
                        />
                        <label htmlFor="checkbox-input">
                          Substance Designer
                        </label>
                      </div>
                    </div>
                    <div className="col-md-6 col-sm-12 items">
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum2}
                        />
                        <label htmlFor="checkbox-input">Lumion</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum2}
                        />
                        <label htmlFor="checkbox-input">GrowFX</label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum2}
                        />
                        <label htmlFor="checkbox-input">
                          3Ds Max Material Library
                        </label>
                      </div>
                      <div className="checkbox">
                        <input
                          type={"checkbox"}
                          id="checkbox-input"
                          onClick={addNum2}
                        />
                        <label htmlFor="checkbox-input">PDF</label>
                      </div>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>

          <div className="upload_image">
            <label>Upload image</label>
            <div className="col-md-12 col-sm-12 loaded_images">
              <div className="images">
                <img src={ChinaTower} alt="pagoda" />
                <img src={SkyCrapers} alt="skycrapers" />
                <img src={Hall} alt="hall" />
              </div>
              <p>
                <small>3/10</small>
              </p>
            </div>
          </div>

          <div className="row">
            <div className="col-md-6  col-sm-12">
              <div className="deadline">
                <label>Deadline</label>
                <div className="row">
                  <div className="date col-md-6 col-sm-12">
                    <input type={"date"} className="form-control" />
                  </div>
                  <div className="checkbox col-md-6 col-sm-12 ">
                    <input type={"checkbox"} id="checkbox-input" required />
                    <label htmlFor="checkbox-input">No fixed deadline</label>
                  </div>
                </div>
              </div>
            </div>

            <div className="col-md-6 col-sm-12">
              <div className="budget">
                <label>Budget</label>
                <div className="money col-md-6 col-sm-12">
                  <img src={Dollar} alt="dolla-sign" />
                  <input type={"text"} required />
                </div>
              </div>
            </div>
          </div>

          <div className="buttons order-btn">
            <button id="button3">Cancel</button>
            <Link to="/completed" className="link">
              <button id="button1">Start a project</button>
            </Link>
          </div>
        </div>
      </main>

      <Footer />
    </div>
  );
};

export default Order;
