import React from "react";
import Header from "./Header";

const AboutUs = () => {
  return (
    <div>
      <Header />
      <div style={{ background: "blue", padding: "200px" }}>
        <div className="container">
          <h2>
            <b style={{ fontSize: "30px", color: "white" }}>Dizipro </b>- bu
            ko'plab imkoniyatlar kaliti. Biz bilan siz o'z uyingizni interior va
            extrerior designini o'zingizning didingizdan kelib chiqqan holda
            yaratishingiz mumkin. Kompaniyamiz nufuzli tashkilotlar bilan hamkor
            qilib kelmoqda. Shuningdek , 25 000dan ortiq muvaffaqiyatli
            loyihalarni amalga oshirgan. Biz bilan hamkorlik qiling va o'z
            loyihalaringizni yarating va bizning maxsus tayyorlangan
            jamoalarimiz bilan birlashish orqali ularni rivojlantiring. Batafsil
            ma'lumotlar bilan kompaniyamizning ijtimoiy tarmoqlardagi
            sahifalarida tanishishingiz mumkin.
          </h2>
        </div>
      </div>
    </div>
  );
};

export default AboutUs;
